body {
  margin: 0;
  line-height: normal;
}

:root {
  /* fonts */
  --font-roboto: Roboto;

  /* font sizes */
  --font-size-sm: 0.88rem;
  --font-size-13xl: 2rem;
  --font-size-base: 1rem;
  --font-size-lg: 1.13rem;
  --font-size-2xl: 1.31rem;
  --font-size-3xs: 0.63rem;
  --font-size-xs: 0.75rem;
  --font-size-smi: 0.81rem;
  --font-size-2xs: 0.69rem;

  /* Colors */
  --color-white: #fff;
  --color-cornflowerblue-100: #2498f4;
  --color-cornflowerblue-200: #2498f3;
  --color-darkorange: #fe7f13;
  --color-gray-100: #777;
  --color-gray-200: rgba(0, 0, 0, 0);
  --color-lightgray: #ccc;
  --color-darkgray: #999;
}
